@font-face {
    font-family: 'Selecta';
    src: url('/wp-content/themes/newfiction/fonts/selecta/woff/Selecta Regular.woff2') format('woff2'),
         url('/wp-content/themes/newfiction/fonts/selecta/woff/Selecta Regular.woff') format('woff');
    font-weight: 400;
}

@font-face {
    font-family: 'Selecta';
    src: url('/wp-content/themes/newfiction/fonts/selecta/woff/Selecta Medium.woff2') format('woff2'),
         url('/wp-content/themes/newfiction/fonts/selecta/woff/Selecta Medium.woff') format('woff');
